@media(max-width:2200px){
    .half2{
      width: 19vw;
         
    }
} 

@media(max-width:767px){
    .contactMid{
        display: none;
    }

    .contactLeftContainer{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .footertext{
        text-align: center;
    }
}

@media (max-width:900px){
    .contactRightImages{
        display: none;
    }

    
}

@media(min-width:901px)and (max-width:1200px){
    .half2{
        width: 20vw;
        margin-top: -4rem;
    }

    
}
@media(min-width:1201px)and (max-width:2000px){
    .half2{
        width: 16vw;
        margin-top: -10rem;
    }
}



